.product-detail-container {
    max-width: 800px;
    margin: 40px auto;
padding: 20px;
}
.product-detail-card {
background-color: white;
border-radius: 8px;
box-shadow: 0 2px 20px rgba(0, 0, 0, 0.1);
overflow: hidden;
}
.product-title {
background-color: #1a237e;
color: white;
padding: 20px;
margin: 0;
font-size: 1.8rem;
text-align: center;
}
.product-info {
padding: 20px;
}
.product-section {
margin-bottom: 30px;
}
.product-section h3 {
color: #1a237e;
border-bottom: 2px solid #e0e0e0;
padding-bottom: 10px;
margin-bottom: 15px;
}
.product-section p {
line-height: 1.6;
color: #333;
}
.product-details-list {
list-style-type: none;
padding: 0;
}
.product-details-list li {
display: flex;
justify-content: space-between;
padding: 12px 0;
border-bottom: 1px solid #eee;
}
.detail-label {
font-weight: bold;
color: #555;
}
.detail-value {
color: #333;
}
.detail-value.price {
font-weight: bold;
color: #e53935;
font-size: 1.2rem;
}
.detail-value.stock {
font-weight: bold;
}
.in-stock {
color: #2e7d32;
}
.out-of-stock {
    color: #c62828;
}
.product-actions {
display: flex;
justify-content: space-between;
padding: 20px;
background-color: #f5f5f5;
}
.action-button {
padding: 10px 20px;
border-radius: 4px;
cursor: pointer;
font-weight: bold;
text-decoration: none;
transition: background-color 0.3s;
border: none;
font-size: 1rem;
}
.back-button {
background-color: #78909c;
color: white;
}
.back-button:hover {
background-color: #607d8b;
}
.action-group {
display: flex;
gap: 10px;
}
.edit-button {
background-color: #ffa726;
color: white;
}
.edit-button:hover {
background-color: #fb8c00;
}
.delete-button {
background-color: #ef5350;
color: white;
}
.delete-button:hover {
background-color: #e53935;
}
.loading, .error {
text-align: center;
padding: 40px;
font-size: 1.2rem;
}
.loading {
color: #1a237e;
}
.error {
color: #c62828;
}
@media screen and (max-width: 600px) {
.product-actions {
flex-direction: column;
gap: 10px;
}
.action-button, .action-group {
width: 100%;
}
.action-group {
    flex-direction: column;
    }
    }