.product-list {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    }
    .product-list h2 {
    color: #1a237e;
    margin-bottom: 20px;
    text-align: center;
    font-size: 2rem;
    }
    .add-button-container {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 20px;
    }
    .add-button {
    background-color: #4caf50;
    color: white;
    padding: 10px 15px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    text-decoration: none;
    font-weight: bold;
    transition: background-color 0.3s;
}
.add-button:hover {
background-color: #45a049;
}
.product-grid {
display: grid;
grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
gap: 20px;
}
.product-card {
background-color: white;
border-radius: 8px;
box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
overflow: hidden;
transition: transform 0.3s ease, box-shadow 0.3s ease;
}
.product-card:hover {
transform: translateY(-5px);
box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}
.product-header {
background-color: #1a237e;
color: white;
padding: 15px;
}
.product-header h3 {
margin: 0;
font-size: 1.3rem;
}
.product-body {
padding: 15px;
}
.product-description {
color: #666;
margin-bottom: 15px;
line-height: 1.4;
height: 60px;
overflow: hidden;
text-overflow: ellipsis;
display: -webkit-box;
-webkit-line-clamp: 3;
-webkit-box-orient: vertical;
}
.product-meta {
display: flex;
justify-content: space-between;
margin-bottom: 10px;
}
.product-price {
font-weight: bold;
color: #e53935;
font-size: 1.2rem;
}
.product-stock {
color: #2e7d32;
font-weight: bold;
}
.product-category {
color: #555;
font-style: italic;
}
.product-actions {
display: flex;
justify-content: space-between;
padding: 15px;
background-color: #f5f5f5;
}
.view-button, .edit-button, .delete-button {
padding: 8px 12px;
border: none;
border-radius: 4px;
cursor: pointer;
font-weight: bold;
text-align: center;
text-decoration: none;
transition: background-color 0.3s;
}
.view-button {
background-color: #42a5f5;
color: white;
}
.view-button:hover {
background-color: #1e88e5;
}
.edit-button {
background-color: #ffa726;
color: white;
}
.edit-button:hover {
background-color: #fb8c00;
}
.delete-button {
background-color: #ef5350;
color: white;
}
.delete-button:hover {
    background-color: #e53935;
    }
    .loading, .error, .no-products {
    text-align: center;
    padding: 40px;
    font-size: 1.2rem;
    }
    .loading {
    color: #1a237e;
    }
    .error {
    color: #c62828;
    }
    .no-products {
    color: #455a64;
    }
    @media screen and (max-width: 768px) {
    .product-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    }
    .product-actions {
    flex-direction: column;
    gap: 10px;
    }
    .view-button, .edit-button, .delete-button {
    width: 100%;
    }
    }